.i-container {
  cursor: zoom-in;
  top: 0;
  left: 0;
  height: 680px;
  width: 1164px;
  border: none;
  border-radius: 20px;
  background: rgba(179, 237, 255, 0.726);
  /* width: 100%;
  height: 100%; */
  margin-top: 1rem;
  overflow: scroll;
  /* background-image: linear-gradient( #57cbff, #fbbc92); */
  /* padding-left: 4rem;
  padding-right: 4rem; */
  padding-bottom: 5rem;
}

.browser-header {
  position: relative;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  height: auto;
}
.browser-tab {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 1rem;
  left: 6.4rem;
  color: white;
  font-size: 1rem;
}
.intro-container {
  display: flex;
  max-width: 600px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 772px;
  align-items: flex-start;
}



.title {
  margin-top: 2rem;
  font-size: 4rem;
  margin-bottom: 0;
}

.sub-title {
  font-size: 1.5rem;
  margin-top: 0;
}

.description {
  width: 30rem;
  overflow-wrap: break-word;
  margin-top: 0;
  text-align: left;
}

a {
  color: rgb(0, 0, 0);
  text-decoration: none;
}

a:link {
  text-decoration: none;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 900;
  color: rgb(0, 0, 0);
}

.section-projects {
  display: flex;
  padding-bottom: 1rem;
  padding-top: 0;
  flex-direction: column;
  /* justify-items: flex-start; */
  align-items: flex-start;
}
.label {
  background: linear-gradient(to right, rgb(0, 0, 0) 50%, rgba(255, 255, 255, 0) 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all .3s ease-out;
  border: 4px solid rgb(0, 0, 0);
  text-decoration: none;
  padding: .5rem;
  padding-left: .75rem;
  padding-right: .75rem;

}

.label:hover {
  text-decoration: none;
  color: #c8c1b8;
  background-position: left bottom;
}

.subheading {
  font-size: 2rem;
  padding-bottom: 30px;
}

.projects {
  padding-bottom: 1rem;
}

.activities {
  padding-bottom: 5px;
}

.activities-container {
  display: flex;
  padding-bottom: 1rem;
  padding-top: 0;
  flex-direction: column;
  align-items: flex-start;
}

.list {
  display: flex;
  width: 65%;
  flex-direction: row;
  padding-bottom: 1rem;
  justify-content: space-between;
}

.list.contact {
  margin-bottom: 2rem;
}

.date {
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 900;
  background-color: rgb(0, 0, 0);
  color: #ffffff;
  padding:.2rem;
  padding-left: .4rem;
  padding-right: .4rem;

}

.item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.projects-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

}
.arrow {
  font-family: 'Oswald';
  font-style: normal;
  color: white;
  padding-left: .1rem;
  padding-right: .2rem;
  font-weight: 600;
}

.name {
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 900;
  font-size: 1.1rem;
  padding-top: .6rem;
  padding-bottom: .6rem;
  color: rgb(0, 0, 0);
}

.fade {
  color: rgba(104, 104, 104, 0.625);

}

.button {
  padding-top: 1rem;
  padding-right: 1rem;
}

.education {
  display: flex;
  padding-bottom: 1rem;
  padding-top: 0;
  flex-direction: column;
  align-items: flex-start;
}

.publications {
  display: flex;
  padding-bottom: 1rem;
  padding-top: 0;
  flex-direction: column;
  align-items: flex-start;
}

.article-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}

.publication-title {
  padding-bottom: 1rem;
}